import { Card, Carousel, Modal, Icon, Menu, Row, Tag } from 'antd';
import React, { Component } from 'react';
import { withRouter } from "react-router";
import { deleteItinerary } from './../../../store/actions/editActions'
import { connect } from "react-redux";
import { cardContainer, carousel, img, item, tagStyle } from './ItinCard.module.scss';
const { Meta } = Card;
const confirm = Modal.confirm

class ItinCard extends Component {

    onDeleteClick = () => {
        let that = this;
        confirm({
            title: "Are you sure you want to delete this itinerary?",
            okText: 'Yes',
            cancelText: 'No',
            onOk() {
                that.props.deleteItinerary(that.props.itin.id)
            },
            onCancel() {
                return false;
            },
        });
    }

    render() {

        let tags = this.props.itin.tags ? this.props.itin.tags.map((tag) => {
            return <Tag className={tagStyle} color="#1890ff">{tag}</Tag>
        }) : [];

        let locationString = this.props.itin && this.props.itin.location && this.props.itin.location.map((location, index) => {
            return <span key={index}>{index + 1 !== this.props.itin.location.length ? location.name + " | " : location.name}</span>
        })

        let images = []

        this.props.itin.collectionOrder.forEach(collectID => {
            this.props.itin.collection[collectID].eventOrder.forEach(eventID => {
                if (this.props.itin.collection[collectID].events[eventID].images) {
                    this.props.itin.collection[collectID].events[eventID].images.forEach(img => images.push(img.url))
                }
            })
        })

        let cover = <Carousel className={carousel} effect={"fade"} afterChange={this.onChange}>
            {
                images.map((photo) => {
                    return (
                        <div><img className={img} alt="example" src={photo} /></div>
                    )
                })
            }
        </Carousel>

        return (
            <div className={item}>
                <Card
                    style={{ marginTop: "1em" }}
                    hoverable
                    cover={images.length > 0 ? cover : null}
                    actions={[<Icon onClick={() => { this.props.history.push('/edit/' + this.props.itin.id) }} type="edit" />, <Icon onClick={() => this.onDeleteClick()} type="delete" />]}
                >
                    <Row>
                        <Meta
                            title={this.props.itin.name}
                            description={locationString}
                        />
                    </Row>
                    <Row className={cardContainer}>
                        {tags.slice(0, 6)}
                    </Row>
                </Card>
            </div >
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        deleteItinerary: (id) => dispatch(deleteItinerary(id))
    }
}

export default withRouter(connect(null, mapDispatchToProps)(ItinCard));